<template>
  <div class="comment">
    <CommentEditor :postId="_id" />

    <div class="comments" v-if="commentCount">
      <h2>댓글 {{ commentCount }}개</h2>
      <ul class="comment_items">
        <CommentSlot
          v-for="comment in comments"
          :key="comment._id"
          :comment="comment"
          :userId="userId"
          :postId="_id"
          :author="author._id"
        />
      </ul>
    </div>
  </div>
</template>

<script setup>
import { defineProps, toRefs } from 'vue'
import CommentEditor from './CommentEditor.vue'
import CommentSlot from './slotdata/CommentSlot.vue'

const props = defineProps({
  post: {
    type: Object,
    required: true,
    default: () => ({})
  },
  userId: {
    type: String,
    required: true,
    default: ''
  },
  comments: {
    type: Array,
    default: () => ([])
  },
  commentCount: {
    type: Number,
    default: 0
  }
})

const { _id, author } = toRefs(props.post)
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .comment {
    .comments {
      margin: 6.4rem 0 0;

      h2 {
        color: var(--list_info-dark);
        font-weight: 400;
        font-size: 1.6rem;
        margin: 0 0 3.2rem 0;
      }
    }
  }
</style>
